@import "../Globalcss//Globalcss.scss";

.DoationBasicDetailFormMContainer {
    margin: 68px 0;
    font-family: $ComfertaFont;

    .DoationBasicDetailFormContainer {
        .DoationBasicDetailFormLSide {
            width: 40%;
            position: fixed;
            min-height: 95vh;
            background-color: #FEEA94;

            .DoationBasicDetailFormLSideC {
                padding: 2rem;

                .DoationBasicDetailFormLogo {
                    img {
                        width: 70px;
                    }
                }

                .DoationBasicDetailFormIntroContent {
                    min-height: 60vh;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    padding: 1rem;

                    h1 {
                        font-size: 64px;
                        width: 100%;
                        // letter-spacing:2px;
                        text-shadow: 0px 1px, 1px 0px, 1px 1px;
                    }

                    p {
                        width: 70%;
                    }
                }

                .DoationBasicDetailFormCourosel {
                    .swiper {
                        width: 80%;
                    }

                    .DoationBasicDetailFormCouroselBody {
                        display: flex;
                        width: 100%;
                        flex-direction: column;
                        border-radius: 10px;
                        background-color: #6CABA3;
                        padding: 1em;

                        p {
                            margin: 0;
                        }

                        .DBDFProfileinfo {
                            display: flex;
                            margin: .8em 0;

                            .profilePic {
                                img {
                                    width: 50px;
                                    height: 50px;
                                    border-radius: 10px;
                                }
                            }

                            .NameAndNgoName {
                                display: flex;
                                flex-direction: column;
                                justify-content: center;
                                margin: 0 1em;

                                .profielName {
                                    h6 {
                                        margin: 0;
                                        font-weight: 700;
                                    }

                                    p {
                                        margin: 0;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        .DoationBasicDetailFormRSide {
            width: 60%;
            position: relative;
            top: 0%;
            left: 40%;

            .DoationBasicDetailFormRSideBody {
                min-height: 100vh;
                display: flex;
                padding: 0em 0;
                flex-direction: column;
                align-items: center;

                h1 {
                    margin: 2em 0 0;
                    text-align: center;
                    width: 55%;
                }

                .DoationBasicDetailFormRSideForm {
                    margin: 3em 0 0 0;
                    width: 60%;


                    .DBDFinputFields {
                        display: flex;
                        flex-direction: column;
                        margin: 2em 0;

                        .MobileOtpVerifyButton {
                            width: 100%;
                            display: flex;
                            flex-direction: row;

                            input {
                                width: 80%;
                                margin-right: 10px;
                            }
                        }

                        label {
                            font-weight: 600;
                            padding-bottom: .5em;
                        }

                        input {
                            border-top: none;
                            border-left: 0;
                            border-right: 0;
                            border-bottom: 1px solid black;
                            // appearance: none;
                            margin: 0;

                            &:focus {
                                outline: none;
                            }
                        }

                        .DBDFGender {
                            margin: 1em 0 0 0;

                            label {
                                font-weight: 400;
                                text-shadow: 0px 0px, 0px 0px, 0px 0px;
                                padding-bottom: .5em;
                                font-size: 17px;
                            }

                            input[type=radio] {
                                display: none;
                            }

                            input:checked+.Male {
                                border: solid 1px #000;
                                color: #F00;
                            }

                            input:checked+.Female {
                                border: solid 1px #000;
                                color: green;
                            }

                            input:checked+.Bisexual {
                                border: solid 1px #000;
                                color: green;
                            }

                            label {
                                border: 1px solid black;
                                padding: 10px 64px;
                                border-radius: 10px;
                                margin: 0 2em 0 0;

                                &:hover {
                                    background-color: antiquewhite;
                                    cursor: pointer;
                                }
                            }


                        }

                        input::-webkit-outer-spin-button,
                        input::-webkit-inner-spin-button {
                            -webkit-appearance: none;
                            margin: 0;
                        }
                    }

                    .DBDFSubmitbuttons {
                        margin: 3rem 0 0 0;
                    }
                }
            }
        }
    }
}


